body {
    background-color: #1f1e1e;
    color: white;
    overflow-x: hidden;
    overflow-y: hidden;
}

h1 {
    text-align: center;
}

.todo-wrapper {
    background-color: #353434;
    padding: 2%;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    margin-top: 3%;
    max-height: 80vh;
    overflow-y: auto;
    box-shadow: 0px 5px 7px rgb(27, 27, 27);
    border-radius: 10px;
}

.todo-input {
    display: flex;
    align-items: center;
    justify-content: center;
    border-bottom: 1px solid gray;
    padding-bottom: 25px;
    margin-bottom: 25px;
}

.todo-input_item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-right: 25px;
    border: none;
    outline: none;
}

.todo-input_item label {
    font-weight: bold;
    margin-bottom: 10px;
}

.todo-input_item input {
    padding: 8px;
    border: none;
    width: 250px;
    border: none;
    outline: none;
    border-bottom: 2px solid rgb(0, 230, 122);
    background-color: #353434;
    color: white;
}

.todo-input_item input:focus {
    outline: none;
    outline: 2px solid rgb(0, 230, 122);
}

.primaryBtn {
    background-color: rgb(0, 230, 122);
    color: white;
    border: none;
    border-radius: 5px;
    margin-top: 25px;
    padding: 10px;
    width: 60px;
    cursor: pointer;
}

.primaryBtn:hover {
    background-color: rgb(4, 196, 106);
}

.secondaryBtn {
    background-color: rgb(78, 81, 80);
    color: white;
    border: none;
    border-radius: 0px;
    margin-top: 25px;
    padding: 10px;
    width: fit-content;
    cursor: pointer;
}

.active {
    background-color: rgb(0, 230, 122);
}

.todo-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.icons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
}

.btn-area {
    margin-bottom: 15px;
}

.todo-list_item {
    background-color: #414040;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px;
    padding-bottom: 10px;
    margin-bottom: 10px;
    box-shadow: 0px 3px 5px rgb(43, 42, 42);
    border-radius: 5px;
}

.todo-list_item h3 {
    font-size: 25px;
    color: rgb(0, 230, 122);
    font-weight: bold;
    margin: 0;
}

.todo-list_item p {
    margin-top: 14px;
    color: rgb(161, 161, 161);
    margin-top: 8px;
}

.icon {
    font-size: 25px;
    cursor: pointer;
}

.icon:hover {
    color: red;
}

.check-icon {
    font-size: 25px;
    margin-left: 10px;
    color: rgb(0, 230, 122);
    cursor: pointer;
}

.check-icon:hover {
    color: rgb(4, 196, 106);
}

.edit_wrapper {
    background-color: #414040;
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.edit_wrapper input,
.edit_wrapper textarea {
    border: 1px solid lightgray;
    padding: 10px;
    margin: 5px;
    border-radius: 3px;

}

.edit_wrapper .primaryBtn {
    margin-left: auto;
    margin-right: auto;
    padding: 10px;
    font-size: 16px;
    width: fit-content;
}

@media screen and (max-width:748px) {


    .App h1 {
        text-align: center;
    }

    .todo-wrapper {

        margin-left: auto;
        margin-right: auto;
        margin-top: 3%;
        max-height: 80vh;
        padding: 20px;

    }

    .todo-input {
        display: flex;
        justify-content: center;
        flex-direction: column;
        width: fit-content;
        overflow: hidden;
    }

    .todo-list {
        font-size: 14px;
    }

    .todo-list_item {
        height: fit-content;
    }

}

